div.numberinput {
  overflow: hidden;
  width: 300px;
  height: 150px;
  position: relative;
  display: inline-block;
}
div.numberinput.focus {
}
textarea.numberinput {
  border: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  outline: none;
  color: rgba(0, 0, 0, 0)
}
#success {
  position: fixed;
  top: 200px;
  right: 0;
  width: 800px;
  text-align: center;
}

#success_box {
  width: 560px;
  height: 540px;
  margin: auto;
  padding: 20px;
}



